.habilidades-container {
  height: 100vh;
  flex-direction: column;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: space-around;
  background-color: #2e1a44;
}

.barrinha-container {
  border: 2px solid white;
  border-radius: 30px;
  flex: 1;
  display: flex;
  flex-direction: row;
}

.habilidade-container {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.preenchimentoBarrinha {
  background-color: white;
  border-radius: 30px;
}
